<template>
  <el-container>
    <el-header>Header</el-header>
    <el-main>
      <el-upload
        class="upload-demo"
        drag
        action="http://localhost/v1/app/fileup"
        :auto-upload="false"
        ref="upload"
        multiple>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传IPA/APK</div>
      </el-upload>
      <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">
      上传到服务器
      </el-button>
    </el-main>
  </el-container>
    
  
  
  <!-- <el-button  >上传到服务器</el-button> -->
 


  <!-- <el-dialog :visible.sync="dialogVisible"> -->
    <!-- <img width="100%" :src="dialogImageUrl" alt=""> -->
  <!-- </el-dialog> -->

</template>

<script>
export default {
  name: 'appupload',
  data() {
    return {
      dialogImageUrl: '',
      dialogVisible: false,
      disabled: false
    };
  },
  methods: {
  handleRemove(file) {
    console.log(file);
  },
  handlePictureCardPreview(file) {
    this.dialogImageUrl = file.url;
    this.dialogVisible = true;
  },
  handleDownload(file) {
    console.log(file);
  },
  submitUpload() {
    var status = this.$refs.upload.submit();
    console.log(status);
  },goBack() {
    console.log('go back');
  }
  }
}
</script>

<style lang="scss">
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  h3{
    margin: 55px 0 20px;
    font-weight: 400;
    color: #1f2f3d;
    font-size: 22px;
  }
  p{
    font-size: 14px;
    color: #5e6d82;
    line-height: 1.5em;
  }
</style>




